@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
:root{--primary: #efb6b2;--secondary: #4e4e4e;--error: #ff4a4a}body{font-family:'Noto Serif';color:#4e4e4e;color:var(--secondary)}.App{max-width:960px;margin:0 auto}.title h1{color:#efb6b2;color:var(--primary);font-size:1.2rem;letter-spacing:2px;font-weight:normal}.title h2,.title p{text-align:center}.title h2{margin-top:60px;font-size:2.6rem}form{margin:30px auto 10px;text-align:center}form label{display:block;width:30px;height:30px;border:1px solid #efb6b2;border:1px solid var(--primary);border-radius:50%;margin:10px auto;line-height:30px;color:#efb6b2;color:var(--primary);font-weight:bold;font-size:24px}form label input{height:0;width:0}form label:hover{background:#efb6b2;background:var(--primary);color:white}form .output{height:60px;font-size:0.8rem}form .error{color:#ff4a4a;color:var(--error)}.progress-bar{height:5px;background:#efb6b2;background:var(--primary);margin-top:20px}.img-grid{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:40px}.img-grid .img-wrap{overflow:hidden;height:350px;position:relative;cursor:pointer}.img-grid img{min-width:100%;min-height:100%;max-width:150%;position:absolute;top:0;left:0}.backdrop{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.5)}.backdrop img{display:block;max-width:60%;max-height:80%;margin:60px auto;box-shadow:2px 1px 20px rgba(0,0,0,0.205)}

