@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap');

:root {
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body {
  font-family: 'Noto Serif';
  color: var(--secondary);
}
.App {
  max-width: 960px;
  margin: 0 auto;
}
.title {
  h1 {
    color: var(--primary);
    font-size: 1.2rem;
    letter-spacing: 2px;
    font-weight: normal;
  }

  h2,
  p {
    text-align: center;
  }

  h2 {
    margin-top: 60px;
    font-size: 2.6rem;
  }
}

/* upload form styles */
form {
  margin: 30px auto 10px;
  text-align: center;

  label {
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid var(--primary);
    border-radius: 50%;
    margin: 10px auto;
    line-height: 30px;
    color: var(--primary);
    font-weight: bold;
    font-size: 24px;

    input {
      height: 0;
      width: 0;
      /* opacity: 0; */
    }
  }

  label:hover {
    background: var(--primary);
    color: white;
  }

  .output {
    height: 60px;
    font-size: 0.8rem;
  }

  .error {
    color: var(--error);
  }
}

/* progress bar styles */
.progress-bar {
  height: 5px;
  background: var(--primary);
  margin-top: 20px;
}

/* image grid styles */
.img-grid {
  //   margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;

  grid-gap: 40px;

  .img-wrap {
    // border: 1px solid pink;
    // height: 0;
    //   /* padding controls height, will always be perfectly square regardless of width */
    // padding: 50% 0;
    overflow: hidden;
    height: 350px;
    position: relative;
    cursor: pointer;
  }

  img {
    min-width: 100%;
    min-height: 100%;
    max-width: 150%;
    position: absolute;
    top: 0;
    left: 0;
  }
}

// /* modal styles */
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);

  img {
    display: block;
    max-width: 60%;
    max-height: 80%;
    margin: 60px auto;
    box-shadow: 2px 1px 20px rgba(0, 0, 0, 0.205);
    // border: 3px solid white;
  }
}
